.header {
  width: 100%;
  min-height: 88px;
  background-color: #1c1c21;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "JetBrainsMono", Arial, sans-serif;
  font-style: normal;
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
}

.component {
  display: flex;
  align-items: center;
}

.text {
  margin-left: 9px;
}

.text_type_profile {
  margin-left: 7px;
}

.secondary {
  color: #8585ad;
  margin-left: 9px;
}

.constructor {
  text-decoration: none;
  display: flex;
  align-items: center;
  margin: 0 48px 0 13px;
  color: #8585ad;
}

.list {
  text-decoration: none;
  display: flex;
  align-items: center;
}

.profile {
  text-decoration: none;
  display: flex;
  align-items: center;
  color: #8585ad;
}

.active {
  color: #fff;
  display: flex;
  align-items: center;
  text-decoration: none;
}

.activeFeed {
  color: #fff;
  margin-left: 9px;
}

.logo {
  margin: 0 309px 0 130px;
  cursor: pointer;
}